.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 7vh;
  background-color: #000304;
  color: #fff;
  font-size: 18px;
  padding: 0 20px;
  box-sizing: border-box;
}

.dropdown {
  position: absolute;
  left:0%;
  display: inline-block;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #000000;
  border: 1px solid #000000;
  min-width: 160px;
  z-index: 1;
}

.dropdown-content a {
  color: #F6F1F1;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover{
  text-decoration: none;
  background-color:#19A7CE;
  color:#F6F1F1;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropbtn {
  background-color: #000000;
  color: #fff;
  padding: 13px;
  height: 7vh;
  font-size: 16px;
  border: none;
  cursor: pointer;
}



